import React,{useState,useRef, useEffect} from 'react';
import {useSelector} from 'react-redux';

const CreatorShape = (props) => {
    const {tshirt} = props;
    const activeColor = useSelector(state => state.tshirtReducer.activeColor);
    const tshirtPattern = useSelector(state => state.tshirtReducer.tshirtPattern);
    const [posX,setPosX] = useState(0);
    const [posY,setPosY] = useState(0);
    const [posXEl,setposXEl] = useState(0);
    const [posYEl,setposYEl] = useState(0);
    const drag = useRef(null);
    

    const mouseMove =(event)=>{
        event.preventDefault();
        const cursorPosX = event.clientX;
        const cursorPosY = event.clientY;
        setPosX(cursorPosX);
        setPosY(cursorPosY);
    }

    const movingElement =()=>{
        const offsetLeft = drag.current.offsetLeft;
        const offsetTop = drag.current.offsetTop;
        const moveX =  posX + offsetLeft;
        const moveY =  posY + offsetTop;
        setposXEl(moveX);
        setposYEl(moveY);
        console.log(moveX);
    }
    
    return ( 
        <>
            <div className='tshirt' onMouseMove={mouseMove} name={tshirt} style={{backgroundColor:activeColor}}>
                <div ref={drag} style={{position:'absolute',left:`${posXEl}px`,top:`${posYEl}px`}} onMouseUp={movingElement} className='tshirt-content'><p>{tshirtPattern}</p>
                </div>
            </div>
        </>
     );
}
 
export default React.memo(CreatorShape);